.BoardContainer {
  width: 500px;
  height: 500px;
  border: 1px solid white;
  border-radius: 10px;
  margin: auto;
  margin-top: 80px;
  -ms-flex-pack: center;
  justify-content: center;
  -ms-flex-align: center;
  align-items: center;
  padding: 10px;
  background-color: white; }

.Board {
  width: 100%;
  height: 100%;
  -webkit-transition: opacity 0.75s;
  -o-transition: opacity 0.75s;
  transition: opacity 0.75s; }

.Controls {
  width: 100%;
  height: 100%;
  -webkit-transition: opacity 0.75s;
  -o-transition: opacity 0.75s;
  transition: opacity 0.75s; }

.Hidden {
  opacity: 0; }

.Shown {
  opacity: 1; }

.ResetButton {
  -webkit-transition: opacity 0.75s;
  -o-transition: opacity 0.75s;
  transition: opacity 0.75s; }



