.ResetButton {
  margin: 25px;
  width: 125px;
  height: 45px;
  font-weight: bold;
  font-size: 20px;
  border-radius: 10px;
  border: 1px solid white;
  background-color: #3f51b5;
  -webkit-box-shadow: 0px 0px 3px 1px white;
  box-shadow: 0px 0px 3px 1px white;
  color: white;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  cursor: pointer; }
  .ResetButton:active {
    -webkit-box-shadow: none;
    box-shadow: none;
    -webkit-transform: translateY(2px);
    -ms-transform: translateY(2px);
    transform: translateY(2px); }
  .ResetButton:focus {
    outline: none; }
  .ResetButton:hover {
    background-color: #a8b5ff;
    color: #3f51b5; }

.Shown {
  display: inline; }

.Hidden {
  display: none; }



